<template>
  <div>
    <!-- dialog -->
    <div style="text-align: center">
      <el-row>
        <el-button
          type="primary"
          plain
          @click="
            dialogFormVisible = true;
            activeName = 'first';
          "
          >Login</el-button
        >
        &emsp;
        <el-button
          type="success"
          plain
          @click="
            dialogFormVisible = true;
            activeName = 'second';
          "
          >Register</el-button
        >
      </el-row>
    </div>

    <el-dialog
      title="Online Shopping Mall"
      :visible.sync="dialogFormVisible"
      width="40%"
      center
    >
      <el-tabs v-model="activeName" @tab-click="handleClick">
        <el-tab-pane label="" name="first"></el-tab-pane>
        <el-tab-pane label="" name="second"></el-tab-pane>
      </el-tabs>
      <span v-if="activeName == 'first'">
        <el-form :model="login_form">
          <div style="text-align: center">
            <el-avatar
              src="https://cube.elemecdn.com/0/88/03b0d39583f48206768a7534e55bcpng.png"
            ></el-avatar>
          </div>
          <el-form-item label="" :label-width="formLabelWidth">
            <el-input
              placeholder=""
              v-model="login_form.name"
              autocomplete="off"
            ></el-input>
          </el-form-item>
          <el-form-item label="" :label-width="formLabelWidth">
            <el-input
              placeholder=""
              v-model="login_form.password"
              show-password
            ></el-input>
          </el-form-item>
        </el-form>
      </span>

      <span v-else-if="activeName == 'second'">
        <el-form :model="register_form">
          <div style="text-align: center">
            <el-avatar
              src="https://cube.elemecdn.com/0/88/03b0d39583f48206768a7534e55bcpng.png"
            ></el-avatar>
          </div>
          <el-form-item label="" :label-width="formLabelWidth">
            <el-input
              placeholder="616,"
              v-model="register_form.name"
              autocomplete="off"
            ></el-input>
          </el-form-item>
          <el-form-item label="" :label-width="formLabelWidth">
            <el-input
              placeholder=""
              v-model="register_form.password"
              show-password
            ></el-input>
          </el-form-item>
          <el-form-item label="" :label-width="formLabelWidth">
            <el-input
              placeholder=""
              v-model="register_form.password_again"
              show-password
            ></el-input>
          </el-form-item>
        </el-form>
      </span>

      <!--  -->
      <el-checkbox id="agree" v-model="checked"
        ><el-link
          type="primary"
          href="https://passport.csdn.net/service"
          ></el-link
        ><el-link type="primary" href="https://help.luogu.com.cn/ula/luogu"
          ></el-link
        ></el-checkbox
      >
      <span v-if="activeName == 'first'">
        <div style="text-align: center" slot="footer" class="dialog-footer">
          <el-button type="primary" @click="login_onSubmit" link>
             
          </el-button>
        </div>
      </span>
      <span v-else-if="activeName == 'second'">
        <div style="text-align: center" slot="footer" class="dialog-footer">
          <el-button type="primary" @click="register_onSubmit">
               
          </el-button>
        </div>
      </span>
    </el-dialog>
  </div>
</template>

<script>
import main from "E:\\6_JS\\\\vueGUI\\vueGUI\\vueproject\\src\\main.js";
const axios = require("axios");

export default {
  data() {
    return {
      dialogFormVisible: false,
      login_form: {
        name: "",
        password: "",
      },
      register_form: {
        name: "",
        password: "",
        password_again: "",
      },
      formLabelWidth: "70px",
      checked: false,
      activeName: "first",
    };
  },
  methods: {
    login_onSubmit() {
      const name = this.login_form.name;
      const password = this.login_form.password;
      if (this.checked == false) {
        this.$message.warning("");
        return 0;
      }
      if (name && password) {
        const param = {
          username: name,
          password: password,
        };
        axios
          .post(main.url + "/user/logging", param, {
            headers: { "Content-Type": "application/x-www-form-urlencoded" },
            emulateJSON: true,
          })
          .then((result) => {
            if (result.data) {
              main.user.username = name;
              main.user.password = password;
              this.$message({
                message: "Hello," + main.user.username,
                type: "success",
                duration: 1000,
              });
              this.$router.push("/main");
            } else {
              this.$alert("", "", {
                confirmButtonText: "",
              });
              this.login_form.name = "";
              this.login_form.password = "";
            }
          })
          .catch(function (error) {
            alert(error);
          });
      } else {
        this.$message.error("");
      }
    },

    register_onSubmit() {
      if (
        this.register_form.name.length < 6 ||
        this.register_form.name.length > 16
      ) {
        this.$message.error("616,");
        this.register_form.name = "";
        return 0;
      }

      if (this.register_form.password !== this.register_form.password_again) {
        this.$message.error("");
        this.register_form.password = "";
        this.register_form.password_again = "";
      } else {
        axios
          .post(
            main.url + "/user/register",
            {
              username: this.register_form.name,
              password: this.register_form.password,
            },
            {
              headers: { "Content-Type": "application/x-www-form-urlencoded" },
              emulateJSON: true,
            }
          )
          .then((result) => {
            if (result.data) {
              main.user.username = this.register_form.name;
              main.user.password = this.register_form.password;
              this.$message({
                message: "Hello," + main.user.username,
                type: "success",
                duration: 1000,
              });
              this.$router.push("/main");
            } else {
              alert("");
            }
            this.register_form.name = "";
            this.register_form.password = "";
            this.register_form.password_again = "";
          })
          .catch((e) => {
            alert(e);
          });
      }
    },
    handleClick(tab) {
      if (tab.label == "") console.log("login " + this.activeName);
      else console.log("register " + this.activeName);
    },
  },
};
</script>

<style>
#agree {
  text-align: left;
}
</style>